class PesSelection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // Customization
      question_count: undefined,
      question_count_selected: undefined,
      psychoeducation: 0,
      recommendation: 0,

      roll_key: props.roll_key,

      // Type of Intervention
      type_original: false,
      type_modified: false,
      custom_disabled: props.custom_disabled,
      setCustomDisabled: props.setCustomDisabled,
      setResultsTypeOf: props.setResultsTypeOf,
      setResultsData: props.setResultsData,

      setStep1Selection: props.setStep1Selection,
      setStep2Survey: props.setStep2Survey,
      setStep2SurveyData: props.setStep2SurveyData,
      setPsychoEducation: props.setPsychoEducation,

      costGagUpdate: props.costGagUpdate,
      setRollKey: props.setRollKey,

      // Question Count:
      question_count_320_hidden: 0,
      question_count_320: 0,
      question_count_160: 0,
      question_count_80: 0,
      question_count_40: 0,
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleInputChange_type = this.handleInputChange_type.bind(this);
    this.handleInputChange_qc = this.handleInputChange_qc.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }


  handleSubmit(event) {

    event.preventDefault();

    // CHECK TYPE

    const is_type_selected_yet = [
      this.state.type_modified,
      this.state.type_original,
    ];

    const sum_is_type_selected_yet = is_type_selected_yet.reduce((accumulator, value) => {
      return accumulator + value;
    }, 0);

    if (sum_is_type_selected_yet == 0) {
      return alert('Please make sure you select a PES version.');
    }

    // Test if TYPE is filled out

    if (this.state.type_original == 1) {

      const original_package = {
        "type_original": this.state.type_original,
        "questions_count_320_hidden": this.state.question_count_320_hidden,
        "psychoeducation": this.state.psychoeducation,
        "recommendation": this.state.recommendation,
      };
      console.log(original_package);
      let original_package_cleaned = Object.entries(original_package).filter(([, bool]) => bool).map(e => e[0]);
      original_package_cleaned.push("no_psychoeducation");
      original_package_cleaned.push("no_recommendation");
      console.log(original_package_cleaned);

      this.state.setStep1Selection(false);
      this.state.setStep2Survey(true);
      this.state.setStep2SurveyData(original_package_cleaned);
    }

    if (this.state.type_modified == 1) {

      // Only pick what is true array


      if (this.state.question_count_320 == 0 && this.state.question_count_160 == 0 && this.state.question_count_80 == 0 && this.state.question_count_40 == 0) {
        return alert('Please choose how many questions you have time to complete.');
      }
      if (this.state.psychoeducation == 0) {
        return alert('Please select Psychoeducation for demonstration purposes.');
      }
      if (this.state.recommendation == 0) {
        return alert('Please select Data-driven recommendations for demonstration purposes.');
      }

      const modified_package = {
        "type_modified": this.state.type_modified,
        "questions_count_320": this.state.question_count_320,
        "questions_count_160": this.state.question_count_160,
        "questions_count_80": this.state.question_count_80,
        "questions_count_40": this.state.question_count_40,
        "psychoeducation": this.state.psychoeducation,
        "recommendation": this.state.recommendation,
      };
      console.log(modified_package);
      const modified_package_cleaned = Object.entries(modified_package).filter(([, bool]) => bool).map(e => e[0]);
      console.log(modified_package_cleaned);

      this.state.setStep1Selection(false);
      this.state.setStep2Survey(true);
      this.state.setPsychoEducation(true);
      this.state.setStep2SurveyData(modified_package_cleaned);

    }
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    console.log(name);
    console.log(this.state.name); // Just broken, but it does update
    //console.log(this.state.recommendation);
    console.log(value);

    this.setState({
      [name]: value
    });
    this.state.costGagUpdate(Math.random() * 100000);
    //console.log(this.state.[name]);
  }

  handleInputChange_type(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    //const name2 = eval("this.state." + name);


    if (name == "type_original") {
      this.setState({
        type_original: 0
      })
      this.setState({
        type_modified: 0
      })

      this.setState({
        [name]: value
      });

      this.setState({
        custom_disabled: 1
      })

      // Set the parameters for the count, psychoeducation, and recommendations
      this.setState({
        question_count_320_hidden: 1
      })
      this.setState({
        question_count_160: 0
      })
      this.setState({
        question_count_320: 0
      })
      this.setState({
        question_count_80: 0
      })
      this.setState({
        question_count_40: 0
      })
      this.setState({
        psychoeducation: 0
      })
      this.setState({
        recommendation: 0
      })

      // Update Gag
      this.state.costGagUpdate(Math.random() * 100000);

    }

    if (name == "type_modified") {
      this.setState({
        type_original: 0
      })
      this.setState({
        type_modified: 0
      })
      this.setState({
        [name]: value
      })

      this.setState({
        custom_disabled: 0
      })


      this.state.costGagUpdate(Math.random() * 100000);
    }



    // Disable and undisable

  }

  handleInputChange_qc(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      question_count_320: 0
    })
    this.setState({
      question_count_160: 0
    })
    this.setState({
      question_count_80: 0
    });
    this.setState({
      question_count_40: 0
    })
    this.setState({
      [name]: value
    });
    this.state.costGagUpdate(Math.random() * 100000);
    // Disable and undisable

  }

  render() {
    return (
      <>
        <span class="app-title"><img className="app-logo" src={icon_logo} />Positive Activities Quiz</span>
        <p class="instructions-box"><b>About</b><br />

          This prototype is based on the Pleasant Events Schedule (PES; <a href="https://psycnet.apa.org/record/1973-02672-001">Lewinsohn, 1972</a>), a 320-item questionnaire for pleasant activities. For a subset of those depressed, actively engaging in pleasant behaviours can reduce symptoms of depression. The PES is a frequent component of behaviour activation therapy.
<br/><br/>I extracted SPES from SI-AA, and added psychoeducation and data-driven components to demonstrate my interest in <b>multi-component evaluation and optimization</b>.

          <br /><br />
          In SPES, the "original" version is a digitized form of the PES with descriptive results. The "modified" version adds a psychoeducation component, a feasibility dimension to each item, and data-driven recommendations.


          Give either a try!
        </p>
        <form className="sound-form" onSubmit={this.handleSubmit}>
          <h2 class="pick-title mb-4">Pick your PES version:</h2>

          <label className="sound-container position-relative four-item-col">
            <div className="sound-icon-text">
              <span class="type-title font-weight-bold"><b>Original</b></span><br />
              <span class="type-desc">320 items</span> </div>
            <input
              className="sound-check-box"
              name="type_original"
              type="checkbox"
              checked={this.state.type_original}
              onChange={this.handleInputChange_type} />
          </label>

          <label className="sound-container position-relative four-item-col">
            <div className="sound-icon-text">
              <span class="type-title font-weight-bold"><b>Modified</b></span><br />
              <span class="type-desc">Customization</span>           </div>
            <input
              className="sound-check-box"
              name="type_modified"
              type="checkbox"
              checked={this.state.type_modified}
              onChange={this.handleInputChange_type} />
          </label>

          <h2 className={`pick-title mt-4 ${this.state.custom_disabled ? "disabled" : ""}`}>Choose the number of questions:</h2>
          <label className="sound-container four-item position-relative">
            <div className="sound-icon-text">

              320
            </div>
            <input
              className="sound-check-box"
              name="question_count_320"
              type="checkbox"
              disabled={this.state.custom_disabled ? true : false}
              checked={this.state.question_count_320}
              onChange={this.handleInputChange_qc} />
          </label>

          <label className="sound-container four-item position-relative">
            <div className="sound-icon-text">

              160
            </div>
            <input
              className="sound-check-box"
              name="question_count_160"
              type="checkbox"
              disabled={this.state.custom_disabled ? true : false}
              checked={this.state.question_count_160}
              onChange={this.handleInputChange_qc} />
          </label>

          <label className="sound-container four-item position-relative">
            <div className="sound-icon-text">

              80
            </div>
            <input
              className="sound-check-box"
              name="question_count_80"
              type="checkbox"
              disabled={this.state.custom_disabled ? true : false}
              checked={this.state.question_count_80}
              onChange={this.handleInputChange_qc} />
          </label>

          <label className="sound-container four-item position-relative">
            <div className="sound-icon-text">

              40
            </div>
            <input
              className="sound-check-box"
              name="question_count_40"
              type="checkbox"
              disabled={this.state.custom_disabled ? true : false}
              checked={this.state.question_count_40}
              onChange={this.handleInputChange_qc} />
          </label>

          <span className={`text-secondary fs-6 fst-italic note-text ${this.state.custom_disabled ? "disabled" : ""}`}>Note: Questions for Modified are randomized.</span>


          <h2 className={`pick-title mt-3 ${this.state.custom_disabled ? "disabled" : ""}`}>Add components:</h2>


          <label className="sound-container component position-relative">
            <div className="sound-icon-text component-title-container">
              <div className="img-container"><img className="icon-sound" src={icon_psychoeducation}></img></div>
              <span class="component-title">Beh. Activation<br />Psychoeducation</span>
            </div>
            <input
              className="sound-check-box"
              name="psychoeducation"
              type="checkbox"
              disabled={this.state.custom_disabled ? true : false}
              checked={this.state.psychoeducation}
              onChange={this.handleInputChange} />
          </label>

          <label className="sound-container component position-relative">
            <div className="sound-icon-text component-title-container">
              <div className="img-container"><img className="icon-sound" src={icon_recommendations}></img></div>
              <span class="component-title">Data-driven<br />Recommendations</span>
            </div>
            <input
              className="sound-check-box"
              name="recommendation"
              type="checkbox"
              disabled={this.state.custom_disabled ? true : false}
              checked={this.state.recommendation}
              onChange={this.handleInputChange} />
          </label>


          <button type="submit" className="mt-5 start-practice">Start SPES</button>
        </form>

        <div class="footer-link mt-3"><a class="fs-6 link-dark text-decoration-none " href="https://full-stack-john.github.io">Return to full-stack-john.github.io</a></div>
      </>
    );
  }
}

